.Massages {
  min-height: 100vh;
  min-width: 100%;
  background-size: cover;
  background-position: top;
  background-image: url("https://i.goopics.net/sfh5eq.jpg");
  position : relative;

  @include mobile-only {
    background-image: url("https://i.goopics.net/grtsho.png");
    background-position: right;
  }

  .Textmassages {
    width: 50%;
    left: 10%;
    bottom: 10%;
    line-height: 2rem;
    position: absolute;
    text-align: left;
    display: flex;
    flex-wrap: wrap;
    gap: 5px;
    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.15);
    background-color: #fdfdfd;
    opacity: 0.8;
    border-radius: 20px;
    padding-top: 20px;
    padding-bottom: 20px;
    padding-left: 20px;

    @include mobile-only {
      padding-top: unset;
      padding-bottom: unset;
      padding-left: unset;
      width: 100%;
      position: static;
      border-radius: 0;
    }

    @include tablette-only {
      padding-top: unset;
      padding-bottom: unset;
      padding-left: unset;
      width: 100%;
      position: static;
      border-radius: 0;
    }

    & h3,
    p {
      color: #493929;
    }
    & h3 {
      @include mobile-only {
        font-size: 18px;
      }
    }
    & p {
      @include mobile-only {
        font-size: 10px;
      }
    }
  }
  .titremassage {
    -webkit-animation: text-focus-in 0.5s cubic-bezier(0.55, 0.085, 0.68, 0.53)
      both;
    animation: text-focus-in 0.5s cubic-bezier(0.55, 0.085, 0.68, 0.53) both;
  }
  .textfin {
    -webkit-animation: text-focus-in 1.5s cubic-bezier(0.55, 0.085, 0.68, 0.53)
      both;
    animation: text-focus-in 1.5s cubic-bezier(0.55, 0.085, 0.68, 0.53) both;
  }
  .textmassage1,
  .textmassage2 {
    -webkit-animation: text-focus-in 1s cubic-bezier(0.55, 0.085, 0.68, 0.53)
      both;
    animation: text-focus-in 1s cubic-bezier(0.55, 0.085, 0.68, 0.53) both;
  }
}
